<template>
  <div id="files-table">
    <p>
      <button type="button" class="btn btn-default" @click="upload">上传</button>
    </p>
    <table class="table table-striped table-bordered table-hover table-condensed">
      <thead>
        <tr>
          <td>文件名</td>
          <td>文件类型</td>
          <td>上传时间</td>
          <td>操作</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="file in files" :key="file._id">
          <td>{{file.name}}</td>
          <td>{{file.type}}</td>
          <td>{{file.createTime}}</td>
          <td>
            <button type="button" class="btn btn-default" :value="file._id" @click="remove">删除</button>
            <button type="button" class="btn btn-default" :value="file._id" @click="download">下载</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'file',
  methods: {
    ...mapActions(['loadFiles', 'showModal', 'removeFile']),
    upload() {
      this.showModal({
        show: true,
        type: 'upload',
        title: '上传文件',
        form: 'file',
        data: {}
      });
    },
    remove(event) {
      if (confirm('确定要删除文件？')) {
        this.removeFile(event.target.value);
      }
    },
    download(event) {
      window.open(`/api/file/download?id=${event.target.value}`);
    }
  },
  computed: mapGetters(['files']),
  created() {
    this.loadFiles();
  }
};
</script>



